import React, { Component } from 'react';

// UI library

import Jumbotron from 'react-bootstrap/Jumbotron';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge';

import Modal from 'react-bootstrap/Modal';

import YouTube from 'react-youtube';

// non-UI library

import moment from 'moment';

// CSS

import './App.css';

// Settings

const itemList = [
  {
    type: 'youtube',
    youtubeId: 'EhIvYd5yoXo',
    title: 'Sunday in the park',
    description: 'Shows App UI and How remote-controling Hexapod work.',
    tags: ['demo', 'app'],
    date: moment("2019-03-17"),
  },

  {
    type: 'youtube',
    youtubeId: 'oaAE5fC09KQ',
    title: 'v2.1 - Leg Assembly',
    description: 'This video demostrates how "Leg" is assembled.',
    tags: ['assembly'],
    date: moment("2019-03-10"),
  },

  {
    type: 'youtube',
    youtubeId: 'To2Y6Mhu-CE',
    title: 'Hexapod v2.1',
    description: 'New movement mode: "Shift", "Climb", "Slow" are supported.',
    tags: ['demo','popular'],
    date: moment("2018-12-08"),
  },

  {
    type: 'image',
    title: '3D parts',
    description: 'Parts in Blue rectangle are for "Body", Parts in Red rectangle are for "one Leg"',
    imgUrl: require('./res/3dparts-s.jpg'),
    fullImgUrl: `${process.env.PUBLIC_URL}/images/3dparts-b.jpg`,
    tags: ['assembly'],
    date: moment("2018-12-08"),
  },

  {
    type: 'youtube',
    youtubeId: 'rrRT6g5XCQ4',
    title: 'Hexapod v2.0',
    description: 'Show "Waltz-like" movement',
    tags: ['demo'],
    date: moment("2018-10-13"),
  },

  {
    type: 'youtube',
    youtubeId: 'mwXX7JwDgVw',
    title: 'Hexapod v2.0',
    description: 'Show "Running" and "Climb" movement',
    tags: ['demo'],
    date: moment("2018-10-10"),
  },

  {
    type: 'youtube',
    youtubeId: 'NJ7DdDEcq3U',
    title: 'Hexapod v2.0',
    description: 'Show various movement.',
    tags: ['demo','popular'],
    date: moment("2018-10-06"),
  },

]

const youtubeOpts = {
  height: '100%',
  width: '100%',
  playerVars: { // https://developers.google.com/youtube/player_parameters
    autoplay: 1,
    disablekb: 1,
    rel: 0,
  }
};

// Code

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      showYoutube: false,
      youtubeId: undefined,
    };
  }

  // ----- UI interaction -----

  getOpenYoutube = (id) => () => {
    this.setState({
      youtubeId: id,
      showYoutube: true,
    })
  }

  handleCloseYoutube = () => {
    this.setState({
      showYoutube: false,
    })
  }

  // ----- UI display -----

  /*
    info.imgUrl     : string
    info.title      : string
    info.description: string
    info.tags       : array of string
    info.date       : moment obj
    info.button     : {label, action}
  */
  createBaseCard = (info) => {
    let imgStyle = {
      height: '300px',
      backgroundPosition: 'center center', 
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${info.imgUrl})`,
      cursor: 'pointer',

    }
    if (info.imgHeight)
      imgStyle.height = `${info.imgHeight}px`

    return (
      <Card bsPrefix="card mb-4">
        <div className='bg-dark' style={imgStyle} onClick={info.button.action}/>
        <Card.Body>
          <div className="d-flex justify-content-between">
            <Card.Title>{info.title}</Card.Title>
            <small className="text-muted">{info.date.format("YYYY-MM-DD")}</small>
          </div>
          <Card.Text>{info.description}</Card.Text>
          <div className="d-flex justify-content-between align-items-center">
            <Button size="sm" variant="outline-primary" onClick={info.button.action}>{info.button.label}</Button>
            <div>
              {
                info.tags.map((e, i) => <Badge key={i} pill variant="secondary">{e}</Badge>)
              }
              <Badge pill variant="info">{info.date.format("YYYY-MM")}</Badge>
            </div>
          </div>
        </Card.Body>
      </Card>
    )
  }

  createCard = (info, index) => {
    let table = {
      youtube: (info) => {
        info.imgUrl = `https://img.youtube.com/vi/${info.youtubeId}/0.jpg`;
        info.imgHeight = 270;
        info.button = { label: 'Youtube', action: this.getOpenYoutube(info.youtubeId)};
        return this.createBaseCard(info);
      },
      image: (info) => {
        let openImage = () => {
          window.location = info.fullImgUrl;
        }
        info.imgHeight = 300;
        info.button = { label: 'Detail Image', action: openImage};
        return this.createBaseCard(info);
      },
    }

    let generator = table[info.type];
    if (generator) {
      return (
        <Col md={6} key={index}>
          {generator(info)}
        </Col>
      )
    }
  }

  render() {

    return (
      <div className="App">
        <Jumbotron>
          <div>
            <h1 className="jumbotron-heading text-outlined">Hexapod Project</h1>
            <ul>
              <li className="jumbotron-text text-outlined">6 legs, each with 3 servos, total 18 degress of freedom</li>
              <li className="jumbotron-text text-outlined">Android / iOS remotely controlled</li>
              <li className="jumbotron-text text-outlined">2S LiPO battery powered</li>
            </ul>
            <p>
              <Button variant="primary" target="_blank" href="https://github.com/SmallpTsai/hexapod-v2-7697">Github</Button>
              <Button variant="primary" target="_blank" href="https://www.youtube.com/channel/UC4nvIRRByxOkgKwkdb99Z8w">Youtube</Button>
            </p>
          </div>
        </Jumbotron>

        <div className="py-5 bg-light">
          <Container>
            <Row>
              {
                itemList.map((info, index) => this.createCard(info, index))
              }
            </Row>
          </Container>
        </div>

        <footer className="text-muted">
          <Container>
            <small><p>This is a personal hobby project. <br/>All source code (including 3D components, circuit diagrams, source code) are opened under GPL license. <br/>Feel free to make one by yourself. <br/>Happy Making!</p></small>
          </Container>
          <Container>
            <p className="float-right">
              <a href="#top">Back to top</a>
            </p>
            <small><p>Powered by &copy; Bootstrap</p></small>
          </Container>
        </footer>



        <Modal 
          show={this.state.showYoutube} 
          onHide={this.handleCloseYoutube}
          dialogClassName="youtube-dialog"
          centered
        >
          <div className="youtube-wrapper">
            <YouTube
              className="youtube-iframe"
              videoId={this.state.youtubeId}
              opts={youtubeOpts}
            />
          </div>
        </Modal>

      </div>
    );
  }
}

export default App;
